<style lang="less" scoped>
  .el-date-range-picker {
  }
  .el-date-editor--datetimerange.el-input__inner {
    width: 400px;
  }
  .el-date-editor--daterange.el-input__inner {
    width: 224px;
  }
  .el-input {
    width: 200px;
  }
</style>
<template>
  <div>
    <div class="page-container">
      <div class="table-filters">
        <div class="filter-item">
          <p class="item">登陆时间</p>
          <el-date-picker
            v-model="checkDate"
            size="small"
            type="datetimerange"
            format="yyyy-MM-dd HH:mm:ss"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
        </div>
        <div class="filter-item">
          <p class="item">工号</p>
          <el-input v-model="filterCondition.leaderName"
                    size="small"
                    placeholder="请输入"></el-input>
        </div>
        <div class="filter-item">
          <p class="item">姓名</p>
          <el-input v-model="filterCondition.name"
                    size="small"
                    placeholder="请输入"></el-input>
        </div>
        <div class="filter-item">
          <p class="item">IP地址</p>
          <el-input v-model="filterCondition.leaderName"
                    size="small"
                    placeholder="请输入"></el-input>
        </div>
        <div class='search-btn'>
          <el-button type="primary"
                     size="small"
                     icon="el-icon-search">查询
          </el-button>
        </div>
      </div>
      <el-table
        :data="tableData"
        size="mini"
        style="width: 100%">
        <el-table-column
          fixed
          type="index"
          label="序号"
          width="60">
        </el-table-column>
        <el-table-column
          prop="type"
          label="姓名">
        </el-table-column>
        <el-table-column
          prop="type"
          label="工号">
        </el-table-column>
        <el-table-column
          prop="create"
          label="部门">
        </el-table-column>
        <el-table-column
          prop="department"
          label="登陆系统">
        </el-table-column>
        <el-table-column
          prop="department"
          label="客户端类型">
        </el-table-column>
        <el-table-column
          prop="update_man"
          label="客户端IP">
        </el-table-column>
        <el-table-column
          prop="status"
          label="登录结果">
          <template slot-scope="scope">
            <span :style="{ color: scope.row.status ? '#00C353' : '#E96767' }">
              {{scope.row.status == '1' ? '成功' : '失败'}}
            </span>
          </template>
        </el-table-column>
        <el-table-column
          prop="update_time"
          label="登录时间">
        </el-table-column>
      </el-table>
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="page.sizes"
        layout="sizes,prev,pager,next"
        :current-page.sync="page.current"
        :total="page.total"
        prev-text="上一页"
        next-text='下一页'>
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  components: {
  },
  data () {
    return {
      page: {
        total: 0,
        size: 10,
        current: 1,
        sizes: [10, 20, 50, 100]
      },
      checkDate: '',
      filterStatus: '',
      // 查询条件
      filterCondition: {
        name: null,
        // 负责人
        leaderName: null,
        filterTime: null,
        status: null
      },
      selectScoreStatus: [
        {
          id: '',
          name: '全部'
        },
        {
          id: 0,
          name: '已禁用'
        }, {
          id: 1,
          name: '已启用'
        }
      ],
      tableData: [
        {
          name: '管理员',
          type: '内置角色',
          status: 1,
          create: '王小虎',
          department: 'IT中心',
          update_time: '2018-08-04 14:33',
          update_man: '张飞雄'
        },
        {
          name: '管理员',
          type: '内置角色',
          status: 1,
          create: '王小虎',
          department: 'IT中心',
          update_time: '2018-08-04 14:33',
          update_man: '张飞雄'
        },
        {
          name: '管理员',
          type: '内置角色',
          status: 0,
          create: '王小虎',
          department: 'IT中心',
          update_time: '2018-08-04 14:33',
          update_man: '张飞雄'
        }
      ]
    }
  },
  methods: {
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
    },
    handleClick (row) {
      console.log(row)
    }
  }
}
</script>



